.Header {
  position: relative;
  z-index: 11;
  display: flex;
  flex-direction: row;
  height: 600px;
}

.Header img {
  width: 100vw;
  height: 600px;
  animation: zoomIn 20s infinite alternate;
  opacity: 0.4;
}



@keyframes zoomIn {
  0% {
    transform: scaleY(1);
  }
  100% {
    transform: scaleY(1.2);
  }
}


.buttonMenu {
  margin-top: 0px;
  left: 0;
margin-left:50px;
width: 210px;
height: 70px;
  position: fixed;
  top: 0;
  z-index: 999;

  background: black;

  color: white;
  padding: 10px 20px;
  transform: skewX(-20deg);
  clip-path: polygon(0 0, 90% 0, 100% 100%, 10% 100%);

  font-size: 150%;
}

.buttonMenu:hover {
  border: 4px solid red;
}
