.Slider{ 
    display: flex;
    flex-direction: column  ;
    height: 2300px;
    width: 100vw;
    background-color: black;
    z-index: 799;
    position: relative; /* добавляем позиционирование */
}


.SliderLine {
    align-self: center;
    display: flex;
    flex-direction: row;
    z-index: 801;
  background: transparent;

    height: 400px;
    width: 100vw;
    margin-top: 200px;
    perspective: 500px; /* добавляем перспективу для 3D-эффекта */
 
  }
  
  .SliderLine2 {
    align-self: center;
    display: flex;
    flex-direction: row;
    z-index: 801;
  background: transparent;

    height: 400px;
    width: 100vw;
    margin-top: 100px;
    perspective: 500px; /* добавляем перспективу для 3D-эффекта */
 
  }
  
  .Slide {
    align-self: center;
    width: 9%;
    height: 100%;
    border: 1px solid rgb(232, 1, 147);
    transform: skewX(-4deg);
    z-index: 999;
    margin-left: 2vw;
    margin-right: 2vw;
    box-shadow: 0 0 900px rgb(230, 153, 233, 0.3); 

  }

  .Slide:nth-child(1) {
    margin-left: 48vw;
  }
  .Slide:active {
    animation: click 2s infinite;
 
  }

  .Slide2 {
    align-self: center;
    width: 9%;
    height: 100%;
    border: 1px solid rgb(232, 1, 147);
    transform: skewX(4deg);
    z-index: 999;
    margin-left: 2vw;
    margin-right: 2vw;
    box-shadow: 0 0 900px rgb(230, 153, 233, 0.3); 

  }
  .Slide2:nth-child(1) {
    margin-left: 45vw;
  }

  .Slide2:active {
    animation: click 2s infinite;
 
  }
  .Slide2:hover {
    /*
    transform: scale(1.1); 
  
    animation: slidershack 15s 1;
    */
    box-shadow: 0 0 900px rgb(236, 230, 237); 
}



  @keyframes click {
    0% {
        transform: rotateY(var(--rotate-angle)); /* вращаем по оси Y */
      }
      100% {
        transform: translateX(-1500%);
      }

  }
  
  /* Добавляем переменную для угла вращения */

.Slide:hover {
    /*
    transform: scale(1.1); 
  
    animation: slidershack 15s 1;
    */
    box-shadow: 0 0 900px rgb(237, 16, 244); 
}


