    0% {
        transform: translateX(0);
      }
      100% {
        transform: translateX(-100%);
      }
}

@keyframes slidershack{
    0% {
        transform: translateY(0);
    }
15% { transform: translateY(-18%);}

    100%{
        transform:translateY(-18%)
    }
}
